.menu {
  display: flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  // Gap between window and console edge
  padding: 20px;
  // Centering
  justify-content: center;
  align-items: center;
  overflow-y: hidden;

  :global {
    .content {
      overflow-y: auto;
    }
    .text {
      min-height: 0;
    }
    .menuButton {
      // color: #FFFFFF;
    }

    .menuOption {
      display: flex;
      width: 100%;

      .pageName {
        flex: auto;
        text-align: center;
      }

      .invSpacer {
        opacity: 0;
        padding-left: 8px;
      }
    }
  }
}
