.projectpage {
  // Gap between title and content
  margin-top: 10px;
  display: flex;
  height: 100%;
  // Fixes top/down resize stay inside window
  overflow-y: hidden;

  :global {
    .projectContent{
      flex: 1;
      margin-right: 10px;
      //Allow text to scroll
      overflow-y: auto;
      height: 100%;
    }
    // Force text element to stay inside flex container
    // Makes overflow work as intended
    .text{
      min-height: 0;
    }
    .text p{
      margin: 0;
    }
    .projectImageContainer{
      flex: 1;
    }
    .projectImage{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

