.intropage {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;

  :global {
    .content {
      // margin-top: 10px;
      // height: 100%;
    }
    .text {
      min-height: 0;
    }
    .text p{
      margin: 1;
    }
    .header {
      flex: 0 0;
      color: yellow;
    }
    .footer {
      flex: 0 0;
    }
    .start {
      color: red;
      flex: 1 0;
    }
    .titleImage {
      flex: 2 1;
      width: 80%;
      min-height: 100px;
      max-width: 400px;
      object-fit: contain;
    }


  }
}

