.window  {
  // Fallbacks apparently
  background: rgb(2,0,36);
  border: 1px groove #FFFFFF;
  // Real result
  background-image: linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(10,0,170,1) 100%);
  border-image: url('../../assets/WindowFrame.png')
                4 /
                4px 4px 4px 4px /
                0px 0px 0px 0px
                stretch;
  padding: 15px 15px;
  image-rendering: pixelated;
  // align center horizontally/vertically
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  box-sizing: border-box;
}

// for filling the rest of the parent
// (space distributed evenly when there is more than 1)
.autoFlex {
  flex: auto;
}
