//Quick Color Change Constants
$_CONSOLE_BG_COLOR: #808080;
$_SCREEN_COLOR: #202020;
$_BEZEL_COLOR: darkgrey;
$_TEXT_COLOR: #FFFFFF;
$_TITLE_COLOR: #c0c0c0;

.consoleLayout {
  background-color: $_BEZEL_COLOR;
  height: 100vh;
  width: 100%;
  position: relative;

  :global {
    .consoleChildren {
      position: absolute;
      width: 90%;
      height: 90%;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      //Minimum supported res
      // min-width: 480px;
      // min-height: 320px;

      .console {
        // width: 100%;
        // height: 100%;
        // box-sizing: border-box;
        // padding: 0 10%;

        // display: flex;
        // flex-direction: column;

        background-color: $_CONSOLE_BG_COLOR;

        :global {
          // .consoleBezel {
          //   width: 100%;
          //   color: $_TITLE_COLOR;
          //   display: flex;
          // }

          // .consoleTopBezel {
          //   align-items: center;
          //   justify-content: center;
          //   font-weight: bold;
          //   font-size: 80px;
          //   height: 25%;
          // }

          // .consoleBottomBezel {
          //   align-items: center;
          //   font-weight: bold;
          //   font-size: 40px;
          //   height: 20%;

            .consoleButton {
              height: 40px;
              width: 40px;
              background-size: 100% 100%;
              background-color: $_CONSOLE_BG_COLOR;
              border: 0;
              cursor: pointer;
              image-rendering: pixelated;
            }
            .consoleButtonM {
              background-image: url('../../assets/HUGE_Button.png');
              margin-right: 10px;
            }
            .consoleButtonA {
              background-image: url('../../assets/MINUS.png');
              margin-left:auto;
              margin-right: 20px;
            }
            .consoleButtonB {
              background-image: url('../../assets/PLUS.png');
            }
          // }

          .consoleScreen {
            width: 100%;
            height: 50%;
            flex: 1;
            background-color: $_SCREEN_COLOR;
            // border-color: $_CONSOLE_BG_COLOR;
            // To place text over image, best idea?
            position: relative;
            border: 1px groove $_BEZEL_COLOR;
            border-image:
                  url('../../assets/HUGE_Frame.png')
                  50 /
                  10px 10px 10px 10px /
                  0px 0px 0px 0px
                  stretch;
            image-rendering: pixelated;

              // Pass down text attributes to pages
              .frameChildren {
                font-family: "Atlantis";
                font-size: 20px;
                color: $_TEXT_COLOR;
                height: 100%;
              }

              // Pass down button attributes to children
              button {
                color: $_TEXT_COLOR;
                background-color: Transparent;
                border: 0;
                cursor: pointer;
              }
              button .selector{
                opacity: 0;
                padding-right: 8px;
              }
              button:hover .selector{
                opacity: 1;
              }
          }
        }
      }
    }
  }
}

@media only screen {
  .consoleLayout{
    :global {
      .consoleChildren {
        // height: 100%;
        // width: 100%;
      }
      .console {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 0 10%;

        display: flex;
        flex-direction: column;
      }
      .consoleBezel {
        width: 100%;
        color: $_TITLE_COLOR;
        display: flex;
      }

      .consoleTopBezel {
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: 60px;
        height: 15%;
      }

      .consoleBottomBezel {
        align-items: center;
        font-weight: bold;
        font-size: 30px;
        height: 10%;

      }
    }
  }
}
      
@media only screen and (max-width: 1024px) {
  .consoleLayout{
    :global {
      .consoleChildren {
        height: 100%;
        width: 100%;
      }
      .console {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 2% 10%;

        display: flex;
        flex-direction: column;
      }
      .consoleBezel {
        width: 100%;
        color: $_TITLE_COLOR;
        display: flex;
      }

      .consoleTopBezel {
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: 30px;
        height: 10%;
      }

      .consoleBottomBezel {
        align-items: center;
        font-weight: bold;
        font-size: 20px;
        padding: 1% 0%;
        height: 10%;

      }
    }
  }
}

@media only screen and (max-width: 420px) {
  .consoleLayout{
    :global {
      .consoleChildren {
        height: 100%;
        width: 100%;
        top: 42%;
      }
      .console {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 18% 5%;

        display: flex;
        flex-direction: column;
      }
      .consoleBezel {
        width: 100%;
        color: $_TITLE_COLOR;
        display: flex;
      }

      .consoleTopBezel {
        align-items: center;
        justify-content: center;
        font-weight: bold;
        font-size: 30px;
        padding: 5% 0%;
        height: 2%;
      }

      .consoleBottomBezel {
        align-items: center;
        font-weight: bold;
        font-size: 20px;
        padding: 5% 0%;
        height: 2%;

        .consoleButtonM {
          margin-right: 10px;
          margin-left: 20px;
        }
        .consoleButtonA {
          margin-left:auto;
          margin-right: 20px;
        }
        .consoleButtonB {
          margin-right: 20px;
        }
      }
    }
  }
}

